import * as Yup from 'yup';
import { useMemo, useEffect, useState } from 'react';

// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';

// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack, Typography, Button, IconButton } from '@mui/material';
import { styled } from '@mui/material/styles';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import CloseIcon from '@mui/icons-material/Close';
import axios from '../../utils/axios';
import useAuth from '../../hooks/useAuth';
import { FormProvider, RHFSwitch } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

export default function AdsSection() {
    const isMountedRef = useIsMountedRef();
    const [selectedImage, setSelectedImage] = useState(null);
    const [imagePreview, setImagePreview] = useState(null);

    const AdsSchema = Yup.object().shape({
        // ads_message is no longer strictly required since we use image
    });
    const { setting } = useAuth();
    const defaultValues = useMemo(
        () => ({
            ads_show: setting?.setting?.ads_show || 0,
            ads_message: setting?.setting?.ads_message || '',
        }),
        [setting]
    );
    const AccessToken = window.localStorage.getItem('accessToken');
    const methods = useForm({
        resolver: yupResolver(AdsSchema),
        defaultValues
    });

    const {
        watch,
        reset,
        setError,
        handleSubmit,
        formState: { errors, isSubmitting },
    } = methods;

    useEffect(() => {
        if (setting?.setting) {
            reset(defaultValues);
            if (setting?.setting?.ads_message) {
                setImagePreview(setting.setting.ads_message);
            }
        }
    }, [setting, defaultValues, reset]);

    const values = watch();

    const handleImageChange = (event) => {
        const file = event.target.files[0];
        if (file) {
            setSelectedImage(file);
            const reader = new FileReader();
            reader.onloadend = () => {
                setImagePreview(reader.result);
            };
            reader.readAsDataURL(file);
        }
    };

    const handleRemoveImage = () => {
        setSelectedImage(null);
        setImagePreview(null);
    };

    const onSubmit = async (data) => {
        try {
            const formData = new FormData();
            formData.append('ads_show', data.ads_show ? 1 : 0);
            formData.append('ads_message', data.ads_message);

            if (selectedImage) {
                formData.append('ads_image', selectedImage);
            }

            await axios.post(`/api/system/message/${AccessToken}/habukhan/secure`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            });
            swal('success', `Ads Banner Updated`, 'success');
        } catch (error) {
            if (isMountedRef.current) {
                setError('afterSubmit', error);
            }
        }
    };

    return (
        <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
            <Grid container spacing={3}>
                <Grid item xs={12} md={8}>
                    {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
                    <Card sx={{ p: 3 }}>
                        <Box
                            sx={{
                                display: 'grid',
                                columnGap: 3,
                                rowGap: 3,
                                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
                            }}
                        >
                            <Typography variant="subtitle2">Ads Banner Image</Typography>

                            <Button
                                component="label"
                                variant="outlined"
                                startIcon={<CloudUploadIcon />}
                                fullWidth
                                sx={{ mb: 2 }}
                            >
                                {imagePreview ? 'Change Banner' : 'Upload Banner'}
                                <VisuallyHiddenInput type="file" accept="image/*" onChange={handleImageChange} />
                            </Button>

                            {imagePreview && (
                                <Box sx={{ position: 'relative', mt: 2, mb: 1 }}>
                                    <IconButton
                                        onClick={handleRemoveImage}
                                        sx={{
                                            position: 'absolute',
                                            top: 8,
                                            right: 8,
                                            bgcolor: 'background.paper',
                                            '&:hover': { bgcolor: 'error.main', color: 'white' },
                                        }}
                                    >
                                        <CloseIcon />
                                    </IconButton>
                                    <img
                                        src={imagePreview}
                                        alt="Ads Preview"
                                        style={{
                                            width: '100%',
                                            maxHeight: 250,
                                            objectFit: 'contain',
                                            borderRadius: 8,
                                            border: '1px solid #e0e0e0',
                                        }}
                                    />
                                </Box>
                            )}

                            <RHFSwitch
                                name="ads_show"
                                labelPlacement="start"
                                label={
                                    <>
                                        <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                                            {values?.ads_show ? 'Ads Enabled' : 'Ads Disabled'}
                                        </Typography>
                                    </>
                                }
                                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
                            />
                        </Box>
                        <Stack alignItems="flex-end" sx={{ mt: 3 }}>
                            <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                                Save Changes
                            </LoadingButton>
                        </Stack>
                    </Card>
                </Grid>
            </Grid>
        </FormProvider>
    );
}
